
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Loading from 'vux/src/components/loading/index.vue'

import baseLoading from 'components/base-loading.vue';
import {
showPayRollDateQuery
} from '@/api/pay-roll-report'
import APP_CONFIGS from '@/utils/app-config'

export default {
name: 'payRollData',
components: {
CellFormPreview,
Group,
Cell,
Datetime,
XTable,
Grid,
GridItem,
Loading,
baseLoading,
},
data() {
return {
list: [
{label: '月固定收入', value: '',},
{label: '固定津贴', value: '',},
{label: '全勤津贴', value: '',},
{label: '奖金', value: '',}],
list1: [
{label: '养老保险', value: '',},
{label: '失业保险', value: '',},
{label: '医疗保险', value: '',},
{label: '住房公积金', value: '',},
{label: '租房', value: '',},
{label: '餐费', value: '',}],
payRollList: '',
pay: '',
deductible: '',
total: '',
value1: '',
currentDay: 1,
currentMonth: 1,
currentYear: 1970,
isLoadingData: true,
isLoadingDataNo:false,
}
},
methods: {

chooseMonth(date){
showPayRollDateQuery({"hrSalaryDate": date,}).then(response => {
if (response.data.success) {
console.log("111111111");
this.payRollList = response.data.rows;
this.list[0].value = response.data.rows[0].hrSalaryBase;
this.list[1].value = response.data.rows[0].hrSalaryAllowance;
this.list[2].value = response.data.rows[0].hrSalaryAttendance;
this.list[3].value = response.data.rows[0].hrSalaryBonus;
this.list1[0].value = response.data.rows[0].hrSalaryEndowmentInsurance;
this.list1[1].value = response.data.rows[0].hrSalaryUnemploymentInsurance;
this.list1[2].value = response.data.rows[0].hrSalaryMedicalInsurance;
this.list1[3].value = response.data.rows[0].hrSalaryHousingFund;
this.list1[4].value = response.data.rows[0].hrSalaryRent;
this.list1[5].value = response.data.rows[0].hrSalaryMeals;
this.pay = response.data.rows[0].hrSalaryPayTotal;
this.deductible = response.data.rows[0].hrSalarySubtractTotal;
this.total = response.data.rows[0].hrSalaryTotal;
this.isLoadingData = false;
this.isLoadingDataNo = false;
this.$vux.loading.hide();
} else {
console.log("222222222");
this.payRollList = [];
this.isLoadingData = false;
this.$vux.loading.hide();
this.isLoadingDataNo = true;
}
}).catch(error => {
console.log("3333333333");
this.isLoadingData = false;
this.$vux.loading.hide();
this.isLoadingDataNo = true;
})

},

payRollChooseDate(val){

let changeVal = val.slice(0, 4) + "-" + val.slice(5, 7);
this.chooseMonth(changeVal);
this.initData(changeVal);
},

initData: function (cur) {
let date;
if (cur) {
date = new Date(cur);
} else {
date = new Date();
}
this.currentDay = date.getDate();
this.currentYear = date.getFullYear();
this.currentMonth = date.getMonth() + 1;
},

preMonth: function (year, month) {



this.$vux.loading.show({});
let d = new Date(this.formatDate(year, month, 1));
d.setDate(0);

this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));

this.chooseMonth(this.formatMonth(d.getFullYear(), d.getMonth() + 1));

this.value1 = this.formatMonth(d.getFullYear(), d.getMonth() + 1).slice(0, 4) + "年" + this.formatMonth(d.getFullYear(), d.getMonth() + 1).slice(5, 7) + "月";
},

nextMonth: function (year, month) {
this.$vux.loading.show({});
let d = new Date(this.formatDate(year, month, 1));
d.setDate(35);

this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));

this.chooseMonth(this.formatMonth(d.getFullYear(), d.getMonth() + 1));

this.value1 = this.formatMonth(d.getFullYear(), d.getMonth() + 1).slice(0, 4) + "年" + this.formatMonth(d.getFullYear(), d.getMonth() + 1).slice(5, 7) + "月";
},


formatDate: function (year, month, day) {
let y = year;
let m = month;
if (m < 10) m = "0" + m;
let d = day;
if (d < 10) d = "0" + d;
return y + "-" + m + "-" + d
},


formatMonth: function (year, month) {
let y = year;
let m = month;
if (m < 10) m = "0" + m;
return y + "-" + m
},
},
mounted() {
},
created: function () {
APP_CONFIGS.getAppToken();

let date = this.$route.query.hrPayrollReportRealSalaryDate;
console.log("这里是date" + date);

this.value1 = date.slice(0, 4) + "年" + date.slice(5, 7) + "月";

this.chooseMonth(date);

this.initData(date);
},
}



