import {fetch} from '@/utils/fetch'

/**
 * 获取薪资报表信息
 * @param {*} params
 */
export const payRollReportQuery = (params) => fetch({
  url: 'api/hrpayrollreport/query',
  method: 'POST',
  data: params
});


/**
 * 获取某个月薪资单
 * @param {*} params
 */
export const showPayRollDateQuery = (params) => fetch({
  url: 'api/hrsalary/query',
  method: 'POST',
  data: params
});




